* {
  margin: 0;
  padding: 0;
  font-family: "Yusei Magic", sans-serif;
}
.navBar {
  /* min-height: 8vh; */
  height: 5%;
  width: 100%;
  background-color: #4988be;
  display: flex;
  align-items: flex-start;
}
.head {
  display: table;

  /* background: none;
  align-items: flex-start; */
}
.head p {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.content {
  display: flex;
  align-items: center;
}

.slider p {
  font-size: 15px;
  padding: 10px 90px;
  margin-bottom: 0;
}
#size {
  margin-left: 60px;
  margin-top: 0;
  padding-top: 0;
}
.speed_slider p {
  font-size: 15px;
  padding: 10px 30px;
  margin-bottom: 0;
  margin-right: 40px;
}
.main {
  height: 480px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  background-color: rgb(250, 250, 250);
}
#bars {
  border-radius: 10px;
}
.navbar-custom {
  background-color: #4988be;
}
